@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* FONT = MONTSERRAT */
 
html, body {
  margin: 0;
  font-family: 'Montserrat', sans-serif;
}


#login-page{width:100%;height:100vh;background-color:#0d0d0d}#login-page .container{padding-top:3rem;display:flex;flex-direction:column;align-items:center}#login-page .container header{display:flex;justify-content:center;align-items:center;margin-bottom:2rem}#login-page .container header img{width:100px;height:100px}#login-page .container header span{font-size:3rem;color:white;font-weight:bold}#login-page .container #login-fb{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);min-width:300px;color:white;background-color:#3b5998;margin-bottom:.5rem}#login-page .container #login-fb:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}#login-page .container .separator{display:flex;align-items:center;text-align:center;min-width:300px;text-transform:uppercase;font-size:.9rem;font-weight:600;color:white;margin-bottom:.5rem}#login-page .container .separator::before,#login-page .container .separator::after{content:" ";flex:1 1;border-bottom:1px solid}#login-page .container .separator::before{margin-right:.5rem}#login-page .container .separator::after{margin-left:.5rem}#login-page .container .separator::before,#login-page .container .separator::after{border-color:white}#login-page .container input{min-width:300px;border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;border:none;padding:5px 1rem;font-size:.9rem;font-weight:600;margin-bottom:.75rem;outline:none;background-color:#282828;color:white}#login-page .container .remember{min-width:300px;display:flex;justify-content:space-between;color:white;font-weight:600;margin-bottom:1rem;position:relative}#login-page .container .remember .toast{top:-4rem;right:-90%}#login-page .container #login{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);background-color:#00d95f;color:white;min-width:150px}#login-page .container #login:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}

.toast{border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;position:absolute;border:white 1px solid;z-index:9999;-webkit-backdrop-filter:blur(10px);backdrop-filter:blur(10px);background-color:rgba(0,0,0,0.6)}.toast .toast-header{background-color:inherit}.toast .toast-header button{border:none;background-color:transparent;font-weight:bold;color:white}.toast .toast-header button.close{display:none}.toast .toast-body{font-size:.6rem}.toggle-btn{width:50px;height:25px;border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:1px solid}.toggle-btn div{width:23px;height:23px;border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;transition:margin-left 1s ease;-webkit-transition:margin-left 1s ease;-moz-transition:margin-left 1s ease;-ms-transition:margin-left 1s ease;-o-transition:margin-left 1s ease}.toggle-btn .toggle{background-color:white;cursor:pointer}.logo{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);-webkit-transform:translate(-50%, -50%);-moz-transform:translate(-50%, -50%);-ms-transform:translate(-50%, -50%);-o-transform:translate(-50%, -50%)}.logo img{animation:scaleLogo 2s ease 1;-webkit-animation:scaleLogo 2s ease 1;transform-origin:50% 50%}@keyframes scaleLogo{0%{}100%{transform:scale(0.1);-webkit-transform:scale(0.1);-moz-transform:scale(0.1);-ms-transform:scale(0.1);-o-transform:scale(0.1)}}.show-filters{margin-top:-.5rem;font-size:.75rem;text-decoration:underline;cursor:pointer}.show-filters:hover{color:white}.filters div{display:flex;align-items:center}.filters div input{margin-left:.5rem;margin-right:.5rem}.filters div label{margin:0;margin-right:1rem}

#sidebar{max-width:250px;height:calc(100vh - 5rem);padding:1rem;background-color:black;display:flex;flex-direction:column;align-items:center;position:fixed;top:0;left:0;color:rgba(255,255,255,0.7);transition:margin-left 1s ease;-webkit-transition:margin-left 1s ease;-moz-transition:margin-left 1s ease;-ms-transition:margin-left 1s ease;-o-transition:margin-left 1s ease}#sidebar .toggle{content:" ";width:20px;height:100px;position:absolute;right:-20px;top:50%;transform:translate(0, -50%);-webkit-transform:translate(0, -50%);-moz-transform:translate(0, -50%);-ms-transform:translate(0, -50%);-o-transform:translate(0, -50%);background-color:inherit;border-top-right-radius:9999px;border-bottom-right-radius:9999px}#sidebar .logo-sidebar{display:flex;align-items:center;margin-bottom:1rem}#sidebar .logo-sidebar img{width:50px;height:50px}#sidebar .logo-sidebar span{font-size:2.5rem;font-weight:800;margin-left:.5rem;color:#1ed760}#sidebar input[type=text]{min-width:90%;border-radius:5px;-webkit-border-radius:5px;-moz-border-radius:5px;-ms-border-radius:5px;-o-border-radius:5px;border:none;padding:5px 1rem;font-size:.9rem;font-weight:600;margin-bottom:.75rem;outline:none;max-width:90%}#sidebar .menu{height:60vh;display:flex;flex-direction:column;align-items:center;justify-content:space-between}#sidebar .menu ul{list-style:none;margin:0;padding:0}#sidebar .menu ul #create-playlist{padding:0.5rem;overflow:hidden}#sidebar .menu ul #create-playlist input{width:100%;margin:0;margin-right:auto;background-color:transparent;color:white;border-bottom:1px solid white;border-radius:0px;-webkit-border-radius:0px;-moz-border-radius:0px;-ms-border-radius:0px;-o-border-radius:0px}#sidebar .menu ul li{width:100%;padding:.5rem;display:flex;align-items:center;font-size:1rem;font-weight:600;cursor:pointer}#sidebar .menu ul li:hover{color:white}#sidebar .menu ul li i,#sidebar .menu ul li ion-icon{font-size:1.5rem;margin-right:.5rem}#sidebar .menu ul li a{color:rgba(255,255,255,0.7);display:flex;align-items:center;text-decoration:none}#sidebar .menu ul li a:hover{color:white}#sidebar .menu .install{width:100%;text-align:center}#sidebar .menu .install i{margin-right:.5rem}#sidebar .menu .install span{font-size:.9rem;text-transform:uppercase;font-weight:700}#sidebar .menu .install hr{background-color:rgba(255,255,255,0.7)}#sidebar .login{width:100%;display:flex;align-items:center;justify-content:space-evenly}#sidebar .login img{border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;width:30px;height:30px}#sidebar .login button{border-radius:9999px;-webkit-border-radius:9999px;-moz-border-radius:9999px;-ms-border-radius:9999px;-o-border-radius:9999px;border:none;padding:5px 10px;font-size:.9rem;font-weight:700;filter:brightness(80%);-webkit-filter:brightness(80%);width:125px;background-color:transparent;color:white;border:2px solid #1ed760}#sidebar .login button:hover{filter:brightness(100%);-webkit-filter:brightness(100%)}

#player{width:100%;height:5rem;padding:.5rem;position:fixed;justify-content:space-between;bottom:0;display:flex;background-color:black}#player .song-info{width:20%;display:flex;align-items:center}#player .song-info img{border-radius:50%;-webkit-border-radius:50%;-moz-border-radius:50%;-ms-border-radius:50%;-o-border-radius:50%;width:50px;height:50px;margin-right:1rem}#player .song-info .info{text-align:center;color:white}#player .song-info .info p{margin:0;font-size:.65rem;font-weight:600}#player .song-info i{color:rgba(255,255,255,0.7);margin-left:1rem}#player .main-controls{width:60%;display:flex;flex-direction:column}#player .main-controls .controls{display:flex;justify-content:center;align-items:center}#player .main-controls .controls i{font-size:1.15rem;margin:0 .5rem;color:rgba(255,255,255,0.7);cursor:pointer}#player .main-controls .controls i:hover{color:white}#player .main-controls .controls .fa-play{font-size:1.75rem}#player .main-controls .time-bar{margin-top:.5rem;display:flex;align-items:center;color:white}#player .main-controls .time-bar .progress{width:100%;height:3px;margin:0 .5rem}#player .side-controls{width:20%;height:100%;display:flex;align-items:center;padding:.5rem}#player .side-controls i{color:white;margin-right:.5rem}#player .side-controls .progress{width:100%;height:3px}


